.profile-container {
  --brand: #86bc25;
  --brand-dark: #6a961d;
  --ink: #0f1d12;
  --ink-muted: #3c4c40;
  --surface: #ffffff;
  --surface-soft: #f2f6f0;
  --stroke: rgba(15, 29, 18, 0.12);
  --glow: rgba(134, 188, 37, 0.2);

  font-family: "Aptos", "Segoe UI", sans-serif;
  color: var(--ink);
  padding: 32px 40px 80px;
  min-height: 100vh;
  background:
    radial-gradient(circle at 12% 12%, rgba(134, 188, 37, 0.15), transparent 45%),
    radial-gradient(circle at 85% 0%, rgba(134, 188, 37, 0.08), transparent 40%),
    linear-gradient(135deg, #f7faf5 0%, #eef4eb 60%, #eaf1e6 100%);
}

.profile-container * {
  font-family: inherit;
}

.profile-shell {
  display: grid;
  grid-template-columns: 260px minmax(0, 1fr);
  gap: 20px;
  align-items: start;
}

.profile-card {
  background: var(--surface);
  border-radius: 20px;
  padding: 24px;
  border: 1px solid rgba(134, 188, 37, 0.2);
  box-shadow: 0 16px 32px rgba(15, 29, 18, 0.12);
  display: grid;
  justify-items: center;
  gap: 10px;
  text-align: center;
}

.profile-name {
  margin: 8px 0 0;
  font-size: 1.3rem;
}

.profile-email {
  margin: 0;
  color: var(--ink-muted);
  font-size: 0.9rem;
}

.profile-header {
  display: grid;
  grid-template-columns: 1fr;
  gap: 16px;
  background: var(--surface);
  border-radius: 20px;
  padding: 24px;
  box-shadow: 0 18px 40px rgba(15, 29, 18, 0.12);
  border: 1px solid rgba(134, 188, 37, 0.2);
}

.profile-picture {
  width: 96px;
  height: 96px;
  border-radius: 18px;
  object-fit: cover;
  border: 3px solid rgba(134, 188, 37, 0.3);
  box-shadow: 0 8px 18px rgba(15, 29, 18, 0.12);
}

.profile-info {
  display: grid;
  gap: 8px;
  max-width: 720px;
}

.profile-info h2 {
  margin: 0 0 12px 0;
  font-size: 1.4rem;
}

.profile-info p {
  margin: 0 0 6px 0;
  color: var(--ink-muted);
  font-size: 0.95rem;
}

.profile-details {
  background: var(--surface);
  border-radius: 20px;
  padding: 24px;
  border: 1px solid rgba(134, 188, 37, 0.2);
  box-shadow: 0 16px 32px rgba(15, 29, 18, 0.12);
}

.details-header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  gap: 12px;
  margin-bottom: 10px;
}

.details-header h3 {
  margin: 0;
}

.details-subtitle {
  color: var(--ink-muted);
  font-size: 0.9rem;
}

.profile-actions {
  margin-top: 16px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.reset-password-button {
  width: fit-content;
  padding: 10px 18px;
  border-radius: 999px;
  border: none;
  background: var(--brand);
  color: #fff;
  cursor: pointer;
  font-weight: 600;
  box-shadow: 0 10px 18px var(--glow);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.reset-password-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 12px 24px rgba(134, 188, 37, 0.28);
}

.reset-password-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.reset-password-status {
  font-size: 14px;
}

.reset-password-status.success {
  color: var(--brand-dark);
}

.reset-password-status.error {
  color: #a32020;
}

.reset-password-status.info {
  color: var(--ink-muted);
}

.phone-actions {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  background: var(--surface-soft);
  border-radius: 14px;
  padding: 16px;
  border: 1px solid rgba(15, 29, 18, 0.08);
}

/* Editable fields */
.profile-fields {
  margin-top: 12px;
  display: grid;
  gap: 10px;
}

.profile-field.compact {
  background: var(--surface-soft);
  border-radius: 12px;
  padding: 12px 14px;
  border: 1px solid rgba(15, 29, 18, 0.08);
  display: grid;
  gap: 8px;
}

.field-meta {
  display: none;
}

.field-label {
  font-weight: 700;
  color: var(--ink);
}

.field-note {
  font-size: 0.85rem;
  color: var(--ink-muted);
}

.field-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
}

.field-info {
  display: grid;
  gap: 4px;
}

.field-value {
  color: var(--ink-muted);
  font-size: 0.95rem;
}

.field-edit-button {
  width: fit-content;
  padding: 6px 12px;
  border-radius: 999px;
  border: 1px solid rgba(134, 188, 37, 0.4);
  background: var(--surface);
  color: var(--brand-dark);
  cursor: pointer;
  font-weight: 600;
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

.field-edit-button.icon-only {
  padding: 6px;
  width: 34px;
  height: 34px;
  justify-content: center;
}

.edit-icon {
  width: 18px;
  height: 18px;
}

.field-edit {
  display: grid;
  gap: 8px;
}

.field-input {
  max-width: 420px;
  padding: 10px 12px;
  border-radius: 10px;
  border: 1px solid rgba(15, 29, 18, 0.2);
}

.field-edit-actions {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.field-save-button {
  width: fit-content;
  padding: 8px 16px;
  border-radius: 999px;
  border: none;
  background: var(--brand);
  color: #fff;
  cursor: pointer;
  font-weight: 600;
  box-shadow: 0 10px 18px var(--glow);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.field-save-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 12px 24px rgba(134, 188, 37, 0.28);
}

.field-save-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.field-cancel-button {
  width: fit-content;
  padding: 8px 16px;
  border-radius: 999px;
  border: 1px solid rgba(15, 29, 18, 0.2);
  background: var(--surface);
  color: var(--ink);
  cursor: pointer;
  font-weight: 600;
}

.field-status {
  font-size: 0.85rem;
}

.field-status.success {
  color: var(--brand-dark);
}

.field-status.error {
  color: #a32020;
}

@media (max-width: 768px) {
  .profile-container {
    padding: 24px 20px 80px;
  }

  .profile-shell {
    grid-template-columns: 1fr;
  }
}

.orders-shell {
  margin-top: 24px;
  background: var(--surface);
  border-radius: 20px;
  padding: 22px;
  border: 1px solid rgba(134, 188, 37, 0.2);
  box-shadow: 0 14px 26px rgba(15, 29, 18, 0.08);
}

.orders-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  flex-wrap: wrap;
}

.orders-tabs {
  display: inline-flex;
  gap: 8px;
}

.orders-tab {
  border: 1px solid rgba(134, 188, 37, 0.3);
  background: var(--surface-soft);
  color: var(--brand-dark);
  padding: 6px 12px;
  border-radius: 999px;
  font-weight: 600;
  cursor: pointer;
}

.orders-tab.active {
  background: var(--brand);
  color: white;
  border-color: var(--brand);
}

.orders-table {
  margin-top: 16px;
  display: grid;
  gap: 10px;
}

.orders-row {
  display: grid;
  grid-template-columns: 1.1fr 0.8fr 0.8fr 1.2fr;
  gap: 10px;
  font-size: 0.9rem;
  align-items: center;
}

.orders-head {
  color: var(--ink-muted);
  font-weight: 600;
  position: sticky;
  top: 0;
  background: var(--surface);
  padding: 8px 0;
  border-bottom: 1px solid rgba(15, 29, 18, 0.08);
}

.status-badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 4px 10px;
  border-radius: 999px;
  background: rgba(134, 188, 37, 0.15);
  color: var(--brand-dark);
  font-weight: 700;
  font-size: 0.8rem;
}

.orders-empty-card {
  border-radius: 14px;
  border: 1px dashed rgba(134, 188, 37, 0.4);
  padding: 16px;
  background: var(--surface-soft);
  display: grid;
  gap: 8px;
  color: var(--ink-muted);
  font-size: 0.9rem;
}

.orders-empty-title {
  font-weight: 700;
  color: var(--ink);
}

.orders-empty-meta {
  display: flex;
  gap: 8px;
  font-size: 0.85rem;
  color: var(--brand-dark);
}
